.App {
  background-color: #fbfbff;
  text-align: left;
  font-family: Arial, sans-serif;
}

.App-header {
  height: 50px;
  background-color: hwb(220 81% 1%);
  color: white;
  padding: 20px;
}


p {
  font-size: 12px;
  margin-top: 2px;
  margin-bottom: 2px;
}

.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 20px;
}

.btn {
  border-radius: 5px;
  background-color: hwb(220 81% 1%);
  padding: 5px;
  padding-right: 10px;
  padding-left: 10px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.kanban-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  margin: 10px;
  width: 200px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
  background-color: rgb(255, 255, 255);
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}

.dropOnClick{
  padding: 5px;
  position: absolute;
  width: 150px;
  color: black;
  background-color: hwb(220 81% 1%);
  border-radius: 10px;
  margin-top: 10px;
  z-index: 10;
}

.selectStyle{
  width: 100px;
  border-radius: 5px;
  background: white;
  padding: 2px 5px;
}


.inline-block-div {
  vertical-align: top;
  display: inline-block;
}

h5 {
  padding-left: 10px;
}